import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AboutView from '../views/AboutView.vue'
import SnackView from '../views/productsViews/SnackView.vue'
import DrinkView from '@/views/productsViews/DrinkView.vue'
import BreadView from '@/views/productsViews/BreadView.vue'
import NewsView from '@/views/NewsView.vue'
import LawView from '@/views/LawView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView
  },
  {
    path: '/news',
    name: 'news',
    component: NewsView
  },
  {
    path: '/laws',
    name: 'laws',
    component: LawView
  },
  {
    path: '/products/snack',
    name: 'snack',
    component: SnackView
  },
  {
    path: '/products/drink',
    name: 'drink',
    component: DrinkView
  },
  {
    path: '/products/bread',
    name: 'bread',
    component: BreadView
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
