import { createStore } from 'vuex'

export default createStore({
  state: {
    searchName: '',
    Cart: [],
    itemExist: false
  },
  mutations: {
    updateSearchName (state, searchName) {
      state.searchName = searchName
    },
    updateQuantity (state) {
      state.quantity++
    },
    updateCart (state, cartItem) {
      state.itemExist = state.Cart.includes(cartItem)
      if (state.itemExist) {
        console.log('이미 카트에 존재하는 상품입니다.')
      } else {
        console.log('상품을 추가합니다.')
        state.Cart.push(cartItem)
      }
    }
  },
  actions: {
    updateQuantity: ({ commit }) => commit('updateQuantity'),
    updateCart: ({ commit }) => commit('updateCart')
  }
})
