<template>
  <div class="home">
    <!-- Section-->
    <section class="py-5">
      <div class="container px-4 px-lg-5 mt-5">
        <div class="row gx-4 gx-lg-5 row-cols-2 row-cols-md-3 row-cols-xl-4 justify-content-center">
          <div class="col mb-5" v-for="snackId in snackIndex" :key="snackId.id">
            <div class="card h-100">
              <!-- Product image-->
              <img class="card-img-top" :src='`https://imgur.com/${snacks[imgUrl][snackId]}.jpg`' alt="..." />
              <!-- Product details-->
              <div class="card-body p-4">
                <div class="text-center">
                  <!-- Product name-->
                  <h5 class="fw-bolder">{{ snacks['name'][snackId] }}</h5>
                  <!-- Product price-->
                  {{ snacks['price'][snackId] }}
                </div>
              </div>
              <!-- Product actions-->
              <div class="card-footer p-4 pt-0 border-top-0 bg-transparent">
                <div class="text-center"><button class="btn btn-outline-dark mt-auto"
                    @click="updateCart(snackId)">구매하기</button></div>
              </div>
            </div>
          </div>
          <div class="col mb-5" v-for="drinkId in drinkIndex" :key="drinkId.id">
            <div class="card h-100">
              <!-- Product image-->
              <!-- imgUrl은 문자열 직접 삽입은 안돼서 data에서 문자열 변수로 사용 -->
              <img class="card-img-top" :src='`https://imgur.com/${drinks[imgUrl][drinkId]}.jpg`' alt="..." />
              <!-- Product details-->
              <div class="card-body p-4">
                <div class="text-center">
                  <!-- Product name-->
                  <h5 class="fw-bolder">{{ drinks['name'][drinkId] }}</h5>
                  <!-- Product price-->
                  {{ drinks['price'][drinkId] }}
                </div>
              </div>
              <!-- Product actions-->
              <div class="card-footer p-4 pt-0 border-top-0 bg-transparent">
                <div class="text-center"><a class="btn btn-outline-dark mt-auto" href="#">구매하기</a></div>
              </div>
            </div>
          </div>
          <div class="col mb-5" v-for="breadId in breadIndex" :key="breadId.id">
            <div class="card h-100">
              <!-- Product image-->
              <img class="card-img-top" :src='`https://imgur.com/${breads[imgUrl][breadId]}.jpg`' alt="..." />
              <!-- Product details-->
              <div class="card-body p-4">
                <div class="text-center">
                  <!-- Product name-->
                  <h5 class="fw-bolder">{{ breads['name'][breadId] }}</h5>
                  <!-- Product price-->
                  {{ breads['price'][breadId] }}
                </div>
              </div>
              <!-- Product actions-->
              <div class="card-footer p-4 pt-0 border-top-0 bg-transparent">
                <div class="text-center"><a class="btn btn-outline-dark mt-auto" href="#">구매하기</a></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { POSITION, useToast } from 'vue-toastification'
import axios from 'axios'
import Josa from 'josa-js'

export default {
  name: 'HomeView',
  setup () {
    const toast = useToast()
    return { toast }
  },
  data: function () {
    return {
      name: '갈樂말樂',
      snacks: [],
      snackNames: [],
      imgUrl: 'imgUrl',
      drinks: [],
      drinkNames: [],
      breads: [],
      breadNames: []
    }
  },

  created () {
    const self = this

    // snack과 drink 전체 불러오기
    axios.get('https://gmhsstore-40a12-default-rtdb.firebaseio.com/products/snack.json')
      .then(function (response) {
        self.snacks = response.data
      })
      .catch(function (error) {
        console.log(error)
      })

    axios.get('https://gmhsstore-40a12-default-rtdb.firebaseio.com/products/drink.json')
      .then(function (response) {
        self.drinks = response.data
      })
      .catch(function (error) {
        console.log(error)
      })

    // snack과 drink 이름만 불러오기 -> search기능 구현을 위한 것
    axios.get('https://gmhsstore-40a12-default-rtdb.firebaseio.com/products/snack/name.json')
      .then(function (response) {
        self.snackNames = response.data
      })
      .catch(function (error) {
        console.log(error)
      })

    axios.get('https://gmhsstore-40a12-default-rtdb.firebaseio.com/products/drink/name.json')
      .then(function (response) {
        self.drinkNames = response.data
      })
      .catch(function (error) {
        console.log(error)
      })

    axios.get('https://gmhsstore-40a12-default-rtdb.firebaseio.com/products/bread.json')
      .then(function (response) {
        self.breads = response.data
      })
      .catch(function (error) {
        console.log(error)
      })

    // bread과 drink 이름만 불러오기 -> search기능 구현을 위한 것
    axios.get('https://gmhsstore-40a12-default-rtdb.firebaseio.com/products/bread/name.json')
      .then(function (response) {
        self.breadNames = response.data
      })
      .catch(function (error) {
        console.log(error)
      })
  },

  computed: {
    // snack array와 drink array의 index를 찾아 전체 배열에 배치시킴
    snackIndex () {
      const selectedName = this.$store.state.searchName
      const snacknewIndex = []; let i
      for (i = 0; i < this.snackNames.length; i++) {
        if (this.snackNames[i].includes(selectedName)) { snacknewIndex.push(i) }
      }
      return snacknewIndex
    },
    drinkIndex () {
      const selectedName = this.$store.state.searchName
      const drinknewIndex = []; let i
      for (i = 0; i < this.drinkNames.length; i++) {
        if (this.drinkNames[i].includes(selectedName)) { drinknewIndex.push(i) }
      }
      return drinknewIndex
    },
    breadIndex () {
      const selectedName = this.$store.state.searchName
      const breadnewIndex = []; let i
      for (i = 0; i < this.breadNames.length; i++) {
        if (this.breadNames[i].includes(selectedName)) { breadnewIndex.push(i) }
      }
      return breadnewIndex
    }
  },
  methods: {
    updateCart (index) {
      const snackName = this.snacks.name[index]
      const josa = Josa.c(snackName.replace(')', ''), '이/가')
      const updateMessage = snackName + josa + ' 추가되었습니다'
      const failedMessage = '이미 카트에 존재하는 상품입니다.'

      this.$store.commit('updateCart', snackName)
      if (!this.$store.state.itemExist) {
        this.toast.success(updateMessage, {
          timeout: 2000,
          position: POSITION.BOTTOM_LEFT
        })
      } else {
        this.toast.error(failedMessage, {
          timeout: 2000,
          position: POSITION.BOTTOM_LEFT
        })
      }
    }
  }
}
</script>
<style>
h5 {
  font-size: 1.1rem !important;
}
</style>
