<template>
  <div class="home">
    <!-- Section-->
    <section class="py-5">
      <div class="container px-4 px-lg-5 mt-5">
        <div class="row gx-4 gx-lg-5 row-cols-2 row-cols-md-3 row-cols-xl-4 justify-content-center">
          <div class="col mb-5" v-for="breadId in breadIndex" :key="breadId.id">
            <div class="card h-100">
              <!-- Product image-->
              <img class="card-img-top" :src='`https://imgur.com/${breads[imgUrl][breadId]}.jpg`' alt="..." />
              <!-- Product details-->
              <div class="card-body p-4">
                <div class="text-center">
                  <!-- Product name-->
                  <h5 class="fw-bolder">{{ breads['name'][breadId] }}</h5>
                  <!-- Product price-->
                  {{ breads['price'][breadId] }}
                </div>
              </div>
              <!-- Product actions-->
              <div class="card-footer p-4 pt-0 border-top-0 bg-transparent">
                <div class="text-center"><a class="btn btn-outline-dark mt-auto" href="#">구매하기</a></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'BreadView',
  created () {
    const self = this

    // bread과 drink 전체 불러오기
    axios.get('https://gmhsstore-40a12-default-rtdb.firebaseio.com/products/bread.json')
      .then(function (response) {
        self.breads = response.data
      })
      .catch(function (error) {
        console.log(error)
      })

    // bread과 drink 이름만 불러오기 -> search기능 구현을 위한 것
    axios.get('https://gmhsstore-40a12-default-rtdb.firebaseio.com/products/bread/name.json')
      .then(function (response) {
        self.breadNames = response.data
      })
      .catch(function (error) {
        console.log(error)
      })
  },
  data: function () {
    return {
      name: '갈樂말樂',
      breads: [],
      breadNames: [],
      imgUrl: 'imgUrl'
    }
  },
  computed: {
    // bread array와 drink array의 index를 찾아 전체 배열에 배치시킴
    breadIndex () {
      const selectedName = this.$store.state.searchName
      const breadnewIndex = []; let i
      for (i = 0; i < this.breadNames.length; i++) {
        if (this.breadNames[i].includes(selectedName)) { breadnewIndex.push(i) }
      }
      return breadnewIndex
    }
  }
}
</script>
<style>
h5 {
  font-size: 1.1rem !important;
}
</style>
