<script>
import { computed } from '@vue/runtime-core'
import { mapState } from 'vuex'

export default {
  name: 'layout-navbar',
  data () {
    return {
      name: '갈樂말樂',
      input: '',
      Cartitem: this.$store.state.Cart,
      quantity: computed(() => this.$store.state.Cart.length)
    }
  },
  computed: {
    ...mapState({
      searchName: state => state.searchName
    })
  },
  methods: {
    updateSearchName (e) {
      this.$store.commit('updateSearchName', e.target.value)
    },
    showItems () {
      for (let i = 0; i <= this.quantity - 1; i++) {
        console.log(this.$store.state.Cart[i])
      }
    }

  }
}
</script>

